@import "reset";
@import "fonts";
@import "vars";

.wrapper {
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
    margin: 0 auto;
    font-family: $font-primary;
}
input, select, button {
    font-family: $font-primary;
    box-sizing: border-box;
     &:focus {
        outline: none;
    }
}
.container {
    max-width: 1170px;
    margin: 0 auto;
  
}
// HEADER
.header {
    color: #fff;
    .container {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        
        // justify-content: space-between;
    }
    width: 100%;    
    background-color: $ugly-blue;
    box-shadow: 0px 3px 7px 0 rgba(1, 1, 1, 0.25);

    // logotype
    &__logo-box {
        margin-right: 35px;
        padding: 21px 6px 17px 11px;
        background-color: #fff;
        box-shadow: 0px 3px 7px 0 rgba(1, 1, 1, 0.25);
        }
    &__logo {
        width: 201px;
        height: 37px;
        }
    // search
    $search-height: 73px;
    .header__search-box {
        display: flex;
        max-width: 534px;
        height: $search-height;
        line-height: $search-height;
        background-color: rgba(0,0,0, .2);
        &:hover  {
            background-color: rgba(0,0,0, .3);
        }

        input, button {
            height: $search-height;
            line-height: $search-height;           
            color: #fff;         
            background-color: inherit;
            border: none;
         }
    }
    
    .header__search-field {
        width: 28vw; 
        height: $search-height;           
        line-height: $search-height;           
        padding-left: 2.25vw;
        font-size: 16px;
        font-weight: 300;
        &::placeholder {
        color: #f6f4f4!important;  
        } 
    }
    .header__search-button {
        padding-right: 27px;
        font-size: 27px;
    }

    // header navbar
    .header__navbar {
        display: flex;
        align-items: center;
        margin-left: auto;
        align-self: center;
    } 
    .header__settings {
        margin-right: 31px;
        font-size: 27px;
        i:hover {
            transform: rotate(270deg);
            transition: transform 0.5s ease-out; 
        }
    }
    .header__notifications {
        position: relative;
        margin-right: 20px;
        font-size: 26px;
    }
    .notifications__number {
        position: absolute;
        top: -2px;
        right: -5px;
        width: 18px;
        height: 18px;
        line-height: 18px;
        font-size: 11.5px;
        text-align: center;
        background-color: #d22423;
        border-radius: 50%;
        &:hover {
        cursor: pointer; 
        }
    }   
    .header__account {
        display: flex;
        align-items: center;
        font-size: 16px;
        color: #fff;
        text-transform: none;
        &:hover {
            cursor: pointer;
        }
        
        &:hover .account__name{
            color: #e3e3e3;
            transition: color 0.05s linear;
        }
        .account__avatar {
            width: 40.8px;
            height: 41px;
            margin-right: 12px;
            overflow: hidden;
        }

        .account__name {
            display: inline-block;
            margin-right: 12px;
        }

    }

    i:hover {
        cursor: pointer; 
    }

}
// NAViIGATION
.nav {
  width: 100%;
  height: 59px;
  line-height: 59px;
  box-shadow: 0px 2px 3px 0 rgba(1, 1, 1, 0.25);
  &__items {
      .active {
      border-bottom: 3px solid $ugly-blue;          
      }

  }
  &__item {
      display: inline-block;
      padding-left: 25px;
      padding-right: 25px;
      &:first-child {
          padding-left: 35px;
      }
  }
  &__item-link {
      color: $text-dark;
      font-family: $font-primary;
      font-size: 16px;
      &:hover {
          color: $ugly-blue;
      }
  }
}



// MAIN

// FOOTER
.footer {
  padding: 21px 0 29px 0;
  background-color: $dark-teal;
  &__nav {
      display: flex;
      justify-content: center;
      align-items: center;
  }

  &__link {
      display: flex;
      align-items: center;
      min-height: 13px;
      padding-right: 25px;
      padding-left: 25px;      
      border-right: 1px solid #fff;
      &:last-child {
          border-right: none;
      }
      a {
        font-size: 12px;
        color: #fff;
        &:hover {
            color: #e3e3e3;
            transition: color 0.05s linear;
        }
      }
  }

  &__copy {
      margin-top: 19px;
      text-align: center;
      color: #fff;
      font-size: 12px;
  }
}
 
@import "media";
